<template>
  <div class="subitem-wrap">
    <div>
      <div class="subitem-wrap__start-time">{{startTime}} - {{endTime}}</div>
    </div>
    <div class="subitem-wrap__infos">
      <div class="subitem-wrap__about">{{title}}</div>
      <div v-html="info"></div>
    </div>
    <div class="subitem-wrap__settings">
      <AppSettings
        :color="'black'"
        :show-plus="false"
        mode="edit-subitem"
        @edit-subitem="onEditSubitem"
        @show-subitem-editor="showSubitemEditor"
        @show-editor="hidePlus"
      ></AppSettings>
    </div>
    <AppForm
        v-if="showSubitemForm"
         class="subitem-wrap__form"
         mode="subitem-form"
        :index-subitem=props.index
        :parent-item-index=props.parentItemIndex
        :edit-subitem="editSubitem"
        :set-info-to-editor="setInfoToEditor"
        @show-plus="showPlus"
        @show-form-editor="showSubitemForm = false"
    ></AppForm>
    <div class="subitem-wrap__speakers">
      <div class="subitem-wrap__visible" v-if="editSubitem">
        <h3>Спикеры</h3>
        <div class="subitem-wrap__active" >
          <h4>Отображать спикеров</h4>
          <AppSwitch v-model="isActiveCheckbox"></AppSwitch>
        </div>
      </div>
      <AppChooseDoctor
          :index-subitem=props.index
          :parent-item-index=props.parentItemIndex
          :show-doctors-form=!showDoctorsForm
          :edit-subitem=!editSubitem
      ></AppChooseDoctor>
    </div>
        <div class="subitem-items" v-if="subitemInfo && false">
          <div class="subitem-item">
                <AppSubItemInfo
                    :item="subitem"
                    v-for="(subitem, subIndex) in subitemInfo"
                    :key="subIndex">
                ></AppSubItemInfo>
          </div>
        </div>
  </div>
<!--    <AppForm></AppForm>-->
</template>

<script setup>
import {defineProps, computed, defineEmits, ref, inject} from "vue"
  import AppSettings from "@/components/AppSettings.vue";
  import AppForm from "@/components/AppForm.vue";
  import AppSubItemInfo from "@/components/AppSubItemInfo.vue";
  import AppSwitch from "@/components/AppSwitch.vue";
  import AppChooseDoctor from "@/components/AppChooseDoctor.vue";

  const items = inject("items")
  const props = defineProps({
    item: Object,
    index: Number,
    parentItemIndex: Number
  })

  const editSubitem = ref(false)

  const subitemInfo = items.value[props.parentItemIndex].programs[props.index].data.subitems

  const showSubitemForm = ref(false)

  const emit = defineEmits(['show-subitem-editor', 'show-plus'])

  const startTime = computed(() => {
    const data = props.item.data
    if (!data) return ""
    const start = data.dateRangeStart
    if (!start) return ""
    return start.slice(11, 16)
  })

  const endTime = computed(() => {
    const data = props.item.data
    if (!data) return ""
    const end = data.dateRangeEnd
    if (!end) return ""
    return end.slice(11, 16)
  })

  const setInfoToEditor = ref(false)

  const showDoctorsForm = ref(false)

  const showDoctorsBtns = ref(false)

  const info = computed( () => {
    const data = props.item.data
    if (!data) return ""
    const info = data.programInfo
    if (!info) return ""
    return info
  } )

  const title = computed( () => {
    const data = props.item.data
    if (!data) return ""
    const info = data.programName
    if (!info) return ""
    return info
  } )

  const isEditorVisible = ref(true)

//открывает форму редактирования
  function onEditSubitem(){
    emit( 'show-subitem-editor')
    showSubitemForm.value = true
    editSubitem.value = true
    setInfoToEditor.value = true
    showDoctorsBtns.value = true
  }

  function hidePlus(){
    isEditorVisible.value = false
  }

  function showPlus(){
    isEditorVisible.value = true
    showSubitemForm.value = false
    showDoctorsBtns.value = false
    editSubitem.value = false
    // alert('отмена')
  }

  function showSubitemEditor(){
    showSubitemForm.value = true
    isEditorVisible.value = false
    // showDoctorsForm.value = false
    // console.log('нажата кнопка редактирования')
  }

</script>


<style scoped>
  .subitem-wrap{
    background-color: #f5f5f5;
    color: black;
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
    align-items: start;
    padding: 15px;
    border-radius: 10px;
    gap: 10px;
    flex-wrap: wrap;
    box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
  }
  .subitem-wrap__infos{
    display: flex;
    width: 80%;
    flex-direction: column;
  }
  .subitem-wrap__about{
    width: 80%;
    font-size: 24px;
    font-weight: 500;
    line-height: 1;
    margin-bottom: 16px;
  }
  .subitem-wrap__form{
    width: 100%;
  }
  .subitem-wrap__speakers{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 10px;
    width: 100%;
  }
  .subitem-wrap__btns{
    display: flex;
    margin-left: auto;
    gap: 10px;
  }
  .subitem-wrap__active{
    display: flex;
    gap: 10px;
  }
  .subitem-wrap__visible{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .subitem-wrap__btns button{
    width: 150px;
    padding: 0.375rem 0.75rem;
    line-height: 2;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
  }
</style>