<template>
  <div class="section-wrap">
    <form @submit.prevent="handleSubmit">
      <AppSwitch v-model="localActive" />
      <input
          v-model="localName"
          type="text"
          placeholder="Название секции"
      />

      <div class="section-buttons">
        <button
            type="button"
            @click="handleCancel"
        >
          Отмена
        </button>

        <button type="submit">
          {{ isEditMode ? 'Обновить' : 'Сохранить' }}
        </button>
      </div>
    </form>
  </div>
</template>

<script setup>
import { ref, defineProps, defineEmits, computed, watch } from "vue";
import AppSwitch from "@/components/AppSwitch.vue";

const emit = defineEmits([
  "hideShowSection",
  "sectionInfo",     // событие для создания
  "updateSection"    // событие для редактирования
]);

const props = defineProps({
  active: Boolean,
  itemName: String,
  showValue: Boolean,
  mode: {
    type: String,
    default: "edit"
  }
});

const localName = ref(props.showValue ? props.itemName : "");
const localActive = ref(props.active ?? false);

watch(
    () => props.itemName,
    (newVal) => {
      if (props.showValue) {
        localName.value = newVal;
      }
    }
);

watch(
    () => props.active,
    (newVal) => {
      localActive.value = newVal;
    }
);

const isEditMode = computed(() => props.mode === "edit");

function handleSubmit() {
  const data = {
    name: localName.value,
    active: localActive.value
  };

  if (isEditMode.value) {
    emit("updateSection", data);
  } else {
    emit("sectionInfo", data);
  }

  emit("hideShowSection", false);
}

function handleCancel() {
  console.log('нажата хуйня')
  emit("hideShowSection", false)
}

</script>

<style scoped>
.section-wrap {
  background-color: #38a3b1;
  padding: 20px;
  border-radius: 20px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 24px;
}

form {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}

input {
  line-height: 1.5;
  border-radius: 5px;
  border: none;
  width: 60%;
  padding: 8px 10px;
}

.section-buttons {
  display: flex;
  gap: 10px;
}

button {
  background: transparent;
  border: none;
  color: white;
  font-size: 16px;
  text-decoration: none;
  transition: 0.3s ease-in-out;
  cursor: pointer;
}

button:hover {
  text-decoration: underline;
}
</style>
