<template>
  <div class="program-item">
    <AppSection v-if="editItem"
      :active="active"
      :item-name="props.item.name"
      :show-value="showSectionValue"
      @hideShowSection="showSection"
      @updateSection="onUpdateSection"
      mode="edit"
    ></AppSection>
    <div class="program-item__wrap" v-else>
      <div class="program-item__title">{{ props.item.name }}</div>
      <AppSettings
          :index="props.itemIndex"
          :show-plus="isEditorVisible"
          @show-editor="toggleEditor"
          @edit-item="changeEditItem"
          mode="edit-item"
      ></AppSettings>
    </div>
    <AppForm
      v-if="showFormEditor"
      :show-form-editor="showFormEditor"
      :item="item"
      :index="props.itemIndex"
      @show-plus="showPlus"
      @update-item="updateItem"
    ></AppForm>
    <div class="program-subitems" v-if="props.item.programs && props.item.programs.length">
      <div class="program-subitem" v-for="(item, index) in props.item.programs" :key="index">
        <AppSubItem
          :item="item"
          :index="index"
          :parent-item-index="props.itemIndex"
        ></AppSubItem>
      </div>
    </div>
  </div>
</template>

<script setup>
import {ref, inject, defineProps, defineEmits} from 'vue'
import AppSettings from "@/components/AppSettings.vue";
import AppSubItem from "@/components/AppSubItem.vue";
import AppSection from "@/components/AppSection.vue";
import AppForm from "@/components/AppForm.vue";

const props = defineProps({
  "item": Object,
  "itemIndex": Number
})



const emits = defineEmits(["updateItem"])

const items = inject("items")
const isEditorVisible = ref(true)
const editItem = ref(false)
const showSectionValue = ref(false)
// const showPlusBtn = ref(true)

function toggleEditor() {
  console.log('показываем форму')
  showFormEditor.value = true
  isEditorVisible.value = false
  console.log(isEditorVisible.value)
}

function updateItem(item, index){
  // console.log(index)
  // console.log(item)
  emits("updateItem", item, index)
}

function showPlus(){
  isEditorVisible.value = true
  showFormEditor.value = false
}

const showFormEditor = ref(false)

function changeEditItem(showInputPlaceholder = false){
  editItem.value = !editItem.value
  showSectionValue.value = showInputPlaceholder
}

function onUpdateSection(updatedData) {
  const currentItem = items.value[props.itemIndex]
  items.value[props.itemIndex] = {
    ...currentItem,
    ...updatedData
  }
  editItem.value = false
}

function showSection(){
  editItem.value = false
}

</script>


<style scoped>
  .program-item{
    padding: 20px;
    margin-bottom: 24px;
    display: flex;
    flex-direction: column;
    background-color: #38A3B1FF;
    border-radius: 20px;
    justify-content: space-between;
    align-items: center;
    font-size: 18px;
    color: white;
  }
  .program-item__wrap{
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
  .program-subitems{
    width: 100%;
  }
</style>