<template>
  <div class="switch-wrapper">
    <label class="switch">
      <input
          type="checkbox"
          :checked="props.modelValue"
          @change="onChange"
      >
      <span class="slider"></span>
    </label>
  </div>
</template>

<script setup>
import {defineProps, defineEmits} from "vue";

  const props = defineProps({
    modelValue: {
      type: Boolean,
      default: false
    }
  })

  const emit = defineEmits(["change-checkbox"])

  function onChange(event) {
    emit("change-checkbox", event.target.checked)
  }
</script>


<style scoped>
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

/* Скрываем стандартный чекбокс */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* Ползунок */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #2563eb;
  transition: .4s;
  border-radius: 34px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: .4s;
  border-radius: 50%;
}

/* Когда чекбокс включён */
input:checked + .slider {
  background-color: #ccc; /* ярко-синий */
}

input:checked + .slider:before {
  transform: translateX(26px);
}
</style>