<template>
  <div class="settings-wrap" :class="props.color === 'black' ? 'color-black' : ''">
    <button v-if="plus && props.showPlus" class="settings-wrap__add" @click="showEditor"><i class="fa-solid fa-plus"></i></button>
    <button class="settings-wrap__edit" @click="handleEdit"><i class="fa-regular fa-pen-to-square"></i></button>
    <button class="settings-wrap__delete" @click="handleDelete()"><i class="fa-solid fa-trash"></i></button>
  </div>
</template>

<script setup>
import {inject, defineProps, defineEmits, ref, watch} from "vue"

  const deleteSection = inject("deleteSection")
  const emit = defineEmits(['show-editor','edit-item', 'edit-subitem', 'show-subitem-editor'])
  const plus = ref(true)

  const props = defineProps({
    index: Number,
    showPlus: Boolean,
    color: String,
    mode: {
      type: String,
      default: "edit-item"
    }
  })

  watch(() => props.showPlus, (val) => {
    plus.value = val
  })

  function showEditor(){
    if(props.mode === "edit-item"){
      emit('show-editor', true)
    }
    else if(props.mode === "edit-subitem"){
      emit('show-subitem-editor', true)
      plus.value = false
    }
  }


  function handleEdit(){

    if(props.mode === "edit-item"){
      emit('edit-item', true)
    }
    else if(props.mode === "edit-subitem"){
      emit('edit-subitem', true)

    }
  }
  function handleDelete() {
    console.log("Удаляем элемент с индексом:", props.index)
    deleteSection(props.index)
  }

</script>

<style scoped>
  .settings-wrap > button{
    background-color: transparent;
    color: white;
    border: none;
    scale: 1;
    transition: .1s ease-in-out;
  }
  .settings-wrap > button:hover{
    scale: 1.1;
  }
  .settings-wrap.color-black i{
    color: black;
  }
</style>