
<template>
  <form class="program-editor">
    <div class="program-editor__wrap">
      <div class="program-editor__active">
        <AppSwitch v-model="isActiveCheckbox"></AppSwitch>
      </div>
      <div class="program-editor__date">
        <VueDatePicker v-model="rangeDate" range />
      </div>
      <div class="program-editor__name input-group">
        <input v-model="programName" class="form-control" type="text" placeholder="Название секции">
      </div>
      <div class="program-editor__btns">
        <button @click.prevent="isEditorVisible = false; emit('showPlus')" type="button" class="btn btn-light">Отмена</button>
        <button @click.prevent="saveEditorInfo" type="button" class="btn btn-light">Сохранить</button>
      </div>
    </div>
    <editor
        v-model="programInfo"
        :id="`editor-${props.index}`"
        apiKey="abps7hxclnjsdampitr91wfxdg75nolmjaex4tt30k0ilxa6"
        :init="{
        plugins: 'advlist anchor autolink charmap code fullscreen help image insertdatetime link lists media preview searchreplace table visualblocks wordcount',
        toolbar: 'undo redo | styles | bold italic underline strikethrough | alignleft aligncenter alignright alignjustify | bullist numlist outdent indent | link image',
        height: 300,
      }"
    />
  </form>
</template>

<script setup>
import AppSwitch from "@/components/AppSwitch.vue";
import VueDatePicker from "@vuepic/vue-datepicker";
import Editor from "@tinymce/tinymce-vue";
import {ref, defineEmits, defineProps, watch, inject} from "vue";

const programName = ref("")
const programInfo = ref("")
const isEditorVisible = ref(false)
const rangeDate = ref([new Date(), null])
const isActiveCheckbox = ref(false)
const items = inject("items")

const emit = defineEmits(['updateItem', 'showPlus', 'show-form-editor'])
const props = defineProps({
  item: Object,
  index: Number,
  "show-form-editor": {
    type: Boolean,
    default: false
  },
  mode: {
    type: String,
    default: "item-form"
  },
  indexSubitem: Number,
  parentItemIndex: Number,
  editSubitem: Boolean,
  setInfoToEditor: Boolean
})

function saveEditorInfo() {
  if(props.mode === 'item-form'){
    const updatedItem = {
      // ...props.item,
      programName: programName.value,
      dateRangeStart: rangeDate.value[0],
      dateRangeEnd: rangeDate.value[1],
      programInfo: programInfo.value,
      isActive: isActiveCheckbox.value
    }

    emit('updateItem', updatedItem, props.index)
    emit('showPlus')
    toggleEditor()
  } else if (props.mode === 'subitem-form'){
    const updatedItem = {
      // ...props.item,
      programName: programName.value,
      dateRangeStart: rangeDate.value[0],
      dateRangeEnd: rangeDate.value[1],
      programInfo: programInfo.value,
      isActive: isActiveCheckbox.value
    }
    console.log(updatedItem);
    console.log('запишем сюда');
    // console.log(items.value[props.parentItemIndex])
    console.log(items.value[props.parentItemIndex].programs[props.indexSubitem].data)
    updateSubitemProgram(items.value[props.parentItemIndex].programs[props.indexSubitem].data, updatedItem)

    emit('show-form-editor');
    emit('showPlus')
  }
}

function toggleEditor() {
  isEditorVisible.value = !isEditorVisible.value
  // console.log(props.item.programs[1].data.programName)
}

function updateSubitemProgram(item, data) {

  // if (!Array.isArray(item.subitems)) {
  //   item.subitems = []
  // }
  //
  // const cleanedItem = JSON.parse(JSON.stringify(data))
  //
  // item.subitems.push({
  //   data: cleanedItem
  // })

  Object.assign(item, JSON.parse(JSON.stringify(data)))
}

watch(
    () => props.showFormEditor,
    (newVal) => {
      isEditorVisible.value = newVal
    },
    { immediate: true }
)

watch(
    () => props.setInfoToEditor,
    (newVal) => {
      if (newVal && props.mode === "subitem-form") {
        const currentItem = items.value[props.parentItemIndex].programs[props.indexSubitem].data;

        if (currentItem) {
          programName.value = currentItem.programName || "";
          programInfo.value = currentItem.programInfo || "";
          rangeDate.value = [
            currentItem.dateRangeStart ? new Date(currentItem.dateRangeStart) : new Date(),
            currentItem.dateRangeEnd ? new Date(currentItem.dateRangeEnd) : null
          ];
          isActiveCheckbox.value = !!currentItem.isActive;
        }
      }
    },
    { immediate: true }
)
</script>

<style scoped>
.program-editor{
  width: 100%;
  margin-top: 20px;
  padding: 15px;
  background: white;
  border-radius: 10px;
}
.program-editor__date{
  width: 33%;
}
.program-editor__wrap{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  gap: 15px;
}
.program-editor__active{
  max-width: 100px;
}
.program-editor__name, .program-editor__btns{
  width: 35%;
}
.program-editor__btns{
  display: flex;
  justify-content: end;
  gap: 10px;
}
.program-editor .dp__input_wrap input{
  border-radius: 5px!important;
}
</style>