<template>
  <div class="choose-doctor" ref="dropdownRoot" v-if="!showChoosedDoctors">
    <!-- Псевдо-input -->
    <div class="selected-box" @click="open = true">
      <span
          v-if="selectedDoctors.length === 0"
          class="placeholder"
      >
        Выберите врачей...
      </span>
      <span
          v-for="doc in selectedDoctors"
          :key="doc"
          class="selected-item"
      >
        {{ doc }}
      </span>
    </div>

    <!-- Выпадающий список -->
    <div v-if="open" class="dropdown">
      <div
          v-for="doc in doctors"
          :key="doc.fio"
          class="doctor-item"
          :class="{ selected: selectedDoctors.includes(doc.fio) }"
          @click="toggleDoctor(doc.fio, doc.id)"
      >
        {{ doc.fio }} ({{ doc.specialty }})
      </div>
    </div>
  </div>
  <div v-else class="choosed-doctors">
    <AppDoctor
        v-for="doctor in selectedDoctorsId"
        :key="doctor"
        :doctor="doctor"
    >
    </AppDoctor>
  </div>
</template>

<script setup>
import { inject, ref, onMounted, onBeforeUnmount, defineProps, watch } from "vue";
import AppDoctor from "@/components/AppDoctor.vue";

const doctors = inject('doctors')
const items = inject("items")
const selectedDoctors = ref([])
const open = ref(false)
const dropdownRoot = ref(null)
const showChoosedDoctors = ref(true)

const selectedDoctorsId = ref([])

const props = defineProps({
  indexSubitem: Number,
  parentItemIndex: Number,
  showDoctorsForm: Boolean,
  editSubitem: Boolean
})

function toggleDoctor(fio, id) {
  if (selectedDoctors.value.includes(fio)) {
    selectedDoctors.value = selectedDoctors.value.filter(d => d !== fio)
    selectedDoctorsId.value = selectedDoctorsId.value.filter(i => i !== id)
  } else {
    selectedDoctors.value.push(fio)
    selectedDoctorsId.value.push(id)
  }
}

function handleClickOutside(e) {
  if (dropdownRoot.value && !dropdownRoot.value.contains(e.target)) {
    open.value = false
  }
}

onMounted(() => {
  document.addEventListener('click', handleClickOutside)
})
onBeforeUnmount(() => {
  document.removeEventListener('click', handleClickOutside)
})

watch(doctors, (newDoctors) => {
  if (!newDoctors || newDoctors.length === 0) return

  if(items.value[props.parentItemIndex].programs[props.indexSubitem].data.doctors){
    const doctorsArr = items.value[props.parentItemIndex].programs[props.indexSubitem].data.doctors

    const existingDoctorIds = doctorsArr.map(function (id) {
      return String(id)
    })


    selectedDoctorsId.value = existingDoctorIds.slice()
    // console.log(selectedDoctorsId.value)

    var selected = []
    for (var i = 0; i < selectedDoctorsId.value.length; i++) {
      var doc = doctors[selectedDoctorsId.value[i]]
      if (!doc || typeof doc.id === 'undefined') continue

      selected.push(doc.fio)

    }
    //
    selectedDoctors.value = selected
  }

}, { immediate: true })

watch(() => selectedDoctorsId.value, (newVal) => {
  items.value[props.parentItemIndex].programs[props.indexSubitem].data.doctors = newVal
  console.log(items.value[props.parentItemIndex].programs[props.indexSubitem].data)
}, { deep: true })

watch(() => props.showDoctorsForm, (newVal) => {
  showChoosedDoctors.value = !newVal
})

watch(() => props.editSubitem, (newVal) => {
  showChoosedDoctors.value = newVal
})

</script>

<style scoped>
.choose-doctor { position: relative; width: 100%; }

.choosed-doctors{
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

/* Псевдо input */
.selected-box {
  display: flex;
  flex-wrap: wrap;         /* перенос слов при нехватке места */
  gap: 4px;
  min-height: 40px;
  padding: 8px 10px;
  border-radius: 5px;
  border: none;
  box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
  cursor: pointer;
  align-items: center;
}
.placeholder {
  cursor: pointer;
  background-color: transparent;
  color: #161414;
}
.selected-item {
  background: #dcdcdc;
  border-radius: 4px;
  padding: 2px 6px;
  font-size: 14px;
  line-height: 1.4;
}

/* Выпадашка */
.dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 10;
  background: white;
  border: 1px solid #ccc;
  padding: 10px;
  width: 100%;
  max-height: 200px;
  overflow-y: auto;
}
.doctor-item {
  padding: 6px 8px;
  cursor: pointer;
  border-radius: 4px;
}
.doctor-item:hover {
  background: #f0f0f0;
}
.doctor-item.selected {
  background: #dcdcdc;
  font-weight: 500;
}
</style>
