function addSection(section){
  items.value.push(section)
  showSection.value = false
  setCookie("items", JSON.stringify(items.value))
}

function updateProgram(updatedItem, index) {

  if (!items.value[index]) {
    items.value[index] = {}
  }

  if (!Array.isArray(items.value[index].programs)) {
    items.value[index].programs = []
  }

  const cleanedItem = JSON.parse(JSON.stringify(updatedItem))
  items.value[index].programs.push({
    data: cleanedItem
  })

  // console.log(items.value)

  setCookie("items", JSON.stringify(items.value))
}

